import React from "react";
import { Model } from "./models";

export class OrbitLayer extends React.Component {
    constructor() {
        super();
        this.originX = 1.5;
        this.originY = 7;
        this.finalX = 11;
        this.finalY = 1;
        this.stepX = (this.finalX - this.originX) / 100.0;
        this.stepY = (this.finalY - this.originY) / 100.0;

        this.originWX = 1.5 -.5;
        this.originWY = 7 + .7;
        this.finalWX = 11 -.5;
        this.finalWY = 1 + .7;
        this.stepWX = (this.finalWX - this.originWX) / 100.0;
        this.stepWY = (this.finalWY - this.originWY) / 100.0;
    }

    calculateEndpoint(origin, step, index) {
        if (index < 101) {
            return origin + (index * step);
        }
        else {
            return origin + (100 * step) - ((index-100) * step);
        }
    }

    calculateAngle(index) {
        if (index < 101) {
            return 130
        }
        else {
            return 130+ 180
        }
    }

    render() {
        return (
            <div>
                <Model parent='#threats-layer'
                    x={this.calculateEndpoint(this.originX, this.stepX, this.props.index)}
                    z={this.calculateEndpoint(this.originY, this.stepY, this.props.index)}
                    y={.1}
                    angle={this.calculateAngle(this.props.index)} />
                <Model parent='#threats-layer'
                    x={this.calculateEndpoint(this.originWX, this.stepWX, this.props.index)}
                    z={this.calculateEndpoint(this.originWY, this.stepWY, this.props.index)}
                    y={.1}
                    angle={this.calculateAngle(this.props.index)} />
            </div>
        );
    }
}