import React from "react";
import { Box } from './box'
import {Text} from './text'

export default class ZonesLayer extends React.Component {
    render() {
        return (
            <div>
                <Box parent='#friendly-layer' color='blue' x={0} y={6} width={.25} height={.50} rotation={30} />
                <Box parent='#friendly-layer' color='blue' x={0.5} y={6} width={.25} height={.50} rotation={30} />
                <Box parent='#friendly-layer' color='blue' x={1} y={6} width={.25} height={.50} rotation={30} />
                {this.props.showLabels ? <Text parent="#friendly-layer" text="CVN" color="yellow" x={0} y={5.5} z={.5} /> : undefined}
            </div>
        );
    }
}