import React from "react";
import { Threat } from './threat'
import { Text } from './text';

export default class ThreatsLayer extends React.Component {
    render() {
        return <div>
            <Threat parent='#threats-layer' iconColor='red' circleColor='red' radius={1.5} z={0.02} x={8} y={1} cY={.1}/>
            <Threat parent='#threats-layer' iconColor='red' circleColor='red' radius={1.5} z={0.05} x={8.5} y={1} cY={.6}/>
            <Threat parent='#threats-layer' iconColor='red' circleColor='red' radius={1.5} z={0.08} x={8.25} y={1.25} cY={1}/>
            {this.props.showLabels
                ? <Text parent="#threats-layer" text="Threat01" color="yellow" x={8} y={1.5} z={1} />
                : undefined}
            {this.props.showLabels
                ? <Text parent="#threats-layer" text="Threat02" color="yellow" x={8.75} y={.5} z={1} />
                : undefined}
            {this.props.showLabels
                ? <Text parent="#threats-layer" text="Threat03" color="yellow" x={7.25} y={.5} z={1} />
                : undefined}
        </div>;
    }
}
